import { useEffect, useState } from "react";
import Header from "./header";
import "./style.css";
import { useParams, useSearchParams } from "react-router-dom";
import Spinner from "../spiner/Spinner";
import {useSazaState , sazaDispatch as dispatch, useSazaFetcher} from "saza-state";
import ErrorViewer from "./error";
import AccItems from "./list";
import AccViewer from "./viewer";

dispatch({
    acc : {
        loading:true,
        error:null,
        data:null,
    },
});


function Acc(){

    const {acc} = useParams();
    const [searchParams, setSearchParams] = useSearchParams();

    const loading = useSazaState(state=>state.acc.loading);
    const error = useSazaState(state=>state.acc.error);
    const data = useSazaState(state=>state.acc.data);

    const loadAccData = () => {

        fetch(`${window.api.url}/dicoms/${acc}/data-v2.json`)
        .then(res=>res.json())
        .then(data=>{
            if(data.status){
                const directories = new Array(data?.directoriesCount || 1);
                data?.displaySet?.forEach((display,index)=>{
                    display.index=index;
                    if(directories[display.pathNumber-1]){
                        directories[display.pathNumber-1].push(display);
                    }else{
                        directories[display.pathNumber-1] = [display];
                    }
                });
                dispatch({
                    acc:{
                        loading:false,
                        error:null,
                        data:data,
                        directories:directories,
                        title:acc,
                    }
                })
            }else{
                dispatch({
                    acc:{
                        loading:false,
                        error:data.message?data.message:"         ",
                        data:data,
                    }
                })
            }
        }).catch(err=>{
            dispatch({
                acc:{
                    loading:false,
                    error:"      ",
                    data:null,
                }
            })
        })
    }

    const showBody = ()=>{
        if(loading){
            return <Spinner />;
        }else if(error){
            return <ErrorViewer message={error} />
        }else if(data){
            if(searchParams.get('d') && searchParams.get('s')){
                return <AccViewer d={searchParams.get('d')} s={searchParams.get('s')} />
            }
            return <AccItems />
        }
    }
    useEffect(()=>{
        loadAccData();
    },[]);


    return (<div className="main">
        {showBody()}
    </div>);
}

export default Acc;