import { useState , useRef, useEffect } from "react";
import Spinner from "../../spiner/Spinner";
import { getBase64Image } from "../list";
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";



function Image({viewerImage,rotate=0,invert=1,setPlaying}){

    useEffect(()=>{
 ///       startAutoDownload();
    },[]);

    const ref = useRef(null);

    const [imgSrc,setImgSrc] = useState(window.images[viewerImage] || viewerImage);
    const [loading,setLoading] = useState(true);

    const imageIsLoaded = ()=>{
        console.log('image loaded')
        window.images[viewerImage] = (viewerImage,getBase64Image(ref.current));
        setPlaying(isPlaying=>{
            return {
                ...isPlaying,
                waiting:false,
            }
        });
        setLoading(false);
    }
    
    useEffect(()=>{
        if(window.images[viewerImage]){
            setImgSrc(window.images[viewerImage]);
        }else{
            setLoading(true);
            setPlaying(isPlay=>{
                return {
                    ...isPlay,
                    waiting:true,
                }
            });
            setImgSrc(viewerImage);
        }
    },[viewerImage]);

    console.log(viewerImage,imgSrc)

    return (<div className="viewer-image">
        <img 
        src={imgSrc} 
        onLoad={e=>imageIsLoaded()} 
        ref={ref} 
        crossOrigin="anonymous"
        style={{
            transform:`rotate(${rotate}deg)`,
            filter:`invert(${invert})`,
            WebkitFilter:`invert(${invert})`,
        }}
        />
        {loading && <Spinner />}
    </div>);

}


export default (props)=>{
    
    return (<TransformWrapper
        initialScale={1}
        initialPositionX={0}
        initialPositionY={0}
        disabled={false}
        options={{
            initialScale: 1,
            minScale: 0.5,
            maxScale: 2
        }}
    >
        {({ zoomIn, zoomOut, resetTransform, ...rest }) => {
            
          
            useEffect(()=>{

                window.resetTransform=resetTransform;
                
            },[]);
            

            return (<TransformComponent>
                <Image 
                {...props}
                />
            </TransformComponent>
        )}}
    </TransformWrapper>)
}



