import React from 'react';
import './Spinner.css';

export default function Spinner(props) {
  return (
    <>
      <div className="spinner-container">
        <div className="spinner"></div>
      </div>
    </>
  );
}
function InlineSpinner(props){
  return (
      <div className="lds-ring inline"
      style={{
          width:props.width+'px',
          height:props.height+'px',
      }}>
          <div style={{
            border:'3px solid '+props.bg,
            borderColor: props.bg +' transparent transparent transparent',
          }}></div>
          <div style={{
            border:'3px solid '+props.bg,
            borderColor: props.bg +' transparent transparent transparent',
          }}></div>
          <div style={{
            border:'3px solid '+props.bg,
            borderColor: props.bg +' transparent transparent transparent',
          }}></div>
          <div style={{
            border:'3px solid '+props.bg,
            borderColor: props.bg +' transparent transparent transparent',
          }}></div>
      </div>
  );
}
export {InlineSpinner}
