function ShowInfo({metaData,closeModal}){
    console.log(metaData)
    
    const exit = e=>e.target.getAttribute('class')==="show-info"&&closeModal()
    const buttonExit = e=>closeModal();
    return (<div 
    className="show-info" 
    onClick={exit}
    >
        <div className="container">
            <div className="header">
                <div className="header-container">
                    <button 
                    className="icon-arrow-left"
                    onClick={buttonExit}
                    ></button>
                    <span>{'Image Information'}</span>
                    <button className=""></button>
                </div>
            </div>
            <div className="show-info-body">
                <div>
                    {Object.entries(metaData).map((data,index)=>{
                        const [key,value] = data;
                        if(!value) return <div key={key} className="display-none"></div>
                        return (<div className="show-info-item" key={key}>
                            <div className="key">{key}</div>
                            <div className="value">{value}</div>
                        </div>)
                    })}

                </div>
            </div>
        </div>

    </div>);
}

export default ShowInfo;