import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import Index from './components';
import Acc from "./components/acc"

function App() {


  return (
    <BrowserRouter>
      <Routes>
        <Route path='/' index element={<Index />} />
        <Route path='/acc/:acc' index element={<Acc />} />
        <Route path='*' index element={<Index />} />
      </Routes>
    </BrowserRouter>
  );
}
export default App;
