import { useSazaState } from "saza-state"
import "./style.css";
import {  useEffect, useState } from "react";
import { useWindowSize } from "@react-hook/window-size";
import Image from "./image";
import ShowInfo from "./showInfo";
import Header from "../header";

import RangeSlider from 'react-range-slider-input';
import 'react-range-slider-input/dist/style.css';

if(window){
    window.images = {};
}


export default function AccViewer({s,d}){

    
    const [rotate,setRotate] = useState(0);
    const [invert,setInvert] = useState(0);
    const [showInfoLayout,setShowInfoLayout] = useState(false);
    const [imageIndex,setImageIndex] = useState(0);
    const [frameRate,setFrameRate] = useState(12);
    const [isPlaying,setIsPlaying] = useState({
        playing:false,
        waiting:false,
    });


    const directories = useSazaState(state=>state.acc.directories);
    const windowSize = useWindowSize();

    let player = null;

    useEffect(()=>{
        if(!isPlaying.playing || isPlaying.waiting) clearInterval(player);
        else player = setInterval(()=>{
            setImageIndex(imageIndex=>imageIndex<displaySet.imageIds.length-1?imageIndex+1:0);
        },1000/frameRate)
        return ()=>clearInterval(player);
    },[isPlaying]);


    const loadImages = async ()=>{
        if( displaySet.imageIds.length>0 ) {
            for(let i=1;i<displaySet.imageIds.length;i++){
                const imageUrl = displaySet.imageIds[i].replace('dicomweb:','').replace('/dicoms/','/dicoms/dcm2jpg/');
                if(!window.images[imageUrl]){
                    try{
                        const imageData = await fetch(imageUrl);
                        const res = await imageData.blob();
                        window.images[imageUrl] =  URL.createObjectURL(res);
                    }catch{
                        window.images = {};
                    }
                }
                
            }
        }
    }

    useEffect(()=>{
        loadImages()
        
    },[]);

    if(!directories || !directories[d] || !directories[d][s]){
        //return <Navigate to={'/'} />
        return <></>
    }

    const displaySet = directories[d][s];

    

    const play = ()=>{
        setIsPlaying(isPlaying=>{
            return {
                ...isPlaying,
                playing:!isPlaying.playing,
            }
        });
    }

    const info = ()=>{
        console.log(showInfoLayout)
        setShowInfoLayout(true)
    }

    const rotatation = ()=>{
        setRotate(rotate=>(rotate+15)%360);
    }

    const invertion = ()=>{
        setInvert(invert=>invert?0:1);
    }

    return (<>
        <Header />
        <div className="viewer" style={{height:`${windowSize[1] - 118 }px`,}}>
            <div className="image-viewer">
                {<Image 
                viewerImage={displaySet.imageIds[imageIndex].replace('dicomweb:','').replace('/dicoms/','/dicoms/dcm2jpg/')} 
                rotate={rotate}
                invert={invert}
                setPlaying={setIsPlaying}
                />}
            </div>
            <div className="image-information">
                <div className="patient-info">
                    <span>PatientName: {displaySet.metadata[imageIndex]?.PatientName}</span>
                    <span>PatientID: {displaySet.metadata[imageIndex]?.PatientID}</span>
                    <span>Gender: {showSex(displaySet.metadata[imageIndex]?.PatientSex)}</span>
                    <span>BirthDate: {displaySet.metadata[imageIndex]?.PatientBirthDate}</span>
                </div>
                <div className="study-info">
                    <span>{displaySet.metadata[imageIndex]?.InstitutionName}</span>
                    <span>{displaySet.metadata[imageIndex]?.StudyDescription}</span>
                    <span>{displaySet.metadata[imageIndex]?.StudyId}</span>
                    {displaySet.metadata[imageIndex]?.AccessionNumber && <span>Ac. Nb: {displaySet.metadata[imageIndex]?.AccessionNumber}</span>}
                    <span>{`${displaySet.metadata[imageIndex]?.StudyDate || displaySet.metadata[imageIndex]?.StudyTime}`}</span>
                </div>
                <div className="image-info">
                    <span>{`Ser: ${displaySet.metadata[imageIndex]?.SeriesNumber}`}</span>
                    <span>{`Img: ${displaySet.metadata[imageIndex]?.InstanceNumber} ${(imageIndex+1)}/${displaySet.imageIds.length}`}</span>
                    <span>{displaySet.metadata[imageIndex]?.Modality +'  '+displaySet.metadata[imageIndex]?.Columns + '' + displaySet.metadata[imageIndex]?.Rows}</span>
                </div>
                <div className="tools-info">
                    <span>{`Rotation: ${rotate}deg`}</span>
                    <span>{`Invert: ${invert?'yes':'no'}`}</span>
                </div>
            </div>
            {displaySet.imageIds.length > 1 && <div className="scrollable-view">
                <input 
                    id="typeinp" 
                    type="range" 
                    min={0} max={displaySet.imageIds.length-1}
                    value={imageIndex} 
                    onChange={e=>setImageIndex(parseInt(e.target.value))}
                    step={1}
                />
            </div>}
        </div>
        <div className="toolbar">
            <div className="toolbar-content">
                <ZoomButton />
                <ToolbarButton disable={displaySet.imageIds.length>1?false:true} label={isPlaying.playing?'Pause':'Play'} icon={isPlaying.playing?'pause':'play'} onClick={()=>play()} />
                <ToolbarButton label={'Invert'} icon={'invert_colors_on'} onClick={invertion} />
                <ToolbarButton label={'Info'} icon={'information-outline'} onClick={()=>info()} />
                <ToolbarButton label={'Rotation'} icon={'rotate'} onClick={rotatation} />
            </div>
        </div>
        {showInfoLayout && <ShowInfo 
        metaData={displaySet.metadata[imageIndex]}
        closeModal={()=>setShowInfoLayout(false)}
         />}
    </>);
}

function ToolbarButton({onClick,icon,label,disable=false}){
    
    return (<button onClick={onClick} className={disable?'disable':'enable'} >
        <div>
            <i className={`icon-${icon}`}></i>
            <span>{label}</span>
        </div>
    </button>);
}

function ZoomButton() {

    const zoom = ()=>{
        if(typeof window.resetTransform === "function"){
            window.resetTransform();
        }else{
           
        }
    }
    return (<ToolbarButton label={'Reset Zoom'} icon={'zoom-in'} onClick={()=>zoom()}  />);
}


function showSex(sex){
    const females = ['F','Female','female','f'];
    const males = ['m','M','male','Male'];
    if(females.includes(sex)) return 'Female';
    else if(males.includes(sex)) return 'Male';
    else return sex;
  }