import { useSearchParams } from "react-router-dom";
import { useSazaState } from "saza-state";

export default function Header(){
    const [searchParams,setSearchParams] = useSearchParams();
    const seriresTitle = useSazaState(state=>state.acc.title);

    const logout = () => {
        localStorage.clear();
        fetch(`${window.api.url}/api/dcm/v1/logout?acc=${seriresTitle}`).then(
            res=>res.json()
        ).then(
            response=>{
                if(response.status){
                    window.location = '/';
                }
            }
        );
    }

    return (<div className="header">
        <div className="header-content">
            <div className="header-items flex">
                {(
                     searchParams.get('d') !== null
                    || searchParams.get('s') !== null
                )
                ? (<>
                    <button onClick={e=>window.history.back()}>
                        <span className="icon-arrow-left"></span>
                        <span>
                        List of Series
                        </span>
                    </button>
                </>)
                : <h1>X-QRay</h1>
                }
            </div>
            <button 
            //className="icon-log-out"
            onClick={logout}
            
            >Exit</button>
        </div>
    </div>);
}