import { useSazaState } from "saza-state";
import Spinner from "../spiner/Spinner";
import { memo, useRef, useState } from "react";
import { Link } from "react-router-dom";

function AccItems(){
    return(<>
        <div className="list">
            <PDFList />
            <DicomList />
        </div>
        {/*<div className="support">
            QX-Ray: (021-2842-0345)
        </div>*/}
        {<div className="list-footer">
            Developed by: <a
             href="https://www.qx-ray.com/q-pacs/"
             >QX-Ray</a> (021-2842-0345)
        </div>}
    </>
    
    );
}

function DicomList(){
    const directories = useSazaState(state=>state.acc.directories) || [];
    return (<div className="">
        {directories.map((directory,index)=>{
            return <Directory key={index} directory={directory} dirIndex={index} />
        })}
    </div>);
}

function Directory({directory,dirIndex}){
    return (<div className="directory">
        <div className="directory-header">Study Date : {directory[0]?.studyDate}</div>
        <div className="items">
            {directory.map((item,index)=>{
                return (<Thumbnial image={item} index={index} dirIndex={dirIndex} key={index} />)
            })}
        </div>
    </div>)
}

function Thumbnial ({image,index,dirIndex}) {

    const accTitle = useSazaState(state=>state.acc.title);

    let imageURL = image.imageIds[0].replace('dicomweb:','').replace('/dicoms/','/dicoms/thumbnail/');

    return (<Link to={`/acc/${accTitle}?d=${dirIndex}&s=${index}`}><div className="item">
        <div className="content flex">
            <ThumbnialImage imageURL={imageURL} />
            <div className="title">
                <span>{image?.metadata[0]?.SeriesDescription ? image?.metadata[0]?.SeriesDescription : "series"}</span>
                <div className='seriesInfo'>
                    <span className='seriesNumber'>{'Series Number : '+image.metadata[0].SeriesNumber}</span>
                    <div className='seriesCount'><span className='icon-count'></span><span>Slide Count :{image.imageIds.length}</span></div>
                </div>
            </div>
        </div>
    </div></Link>);
}

const ThumbnialImage = ({imageURL})=>{
    const ref = useRef(null);
    const [loading,setLoading] = useState(true);
    //const newImageUrl = 'https://picsum.photos/200';
    const [imgSrc,setImgSrc] = useState(localStorage.getItem(imageURL) || imageURL);

    const imageIsLoaded = ()=>{
        try{
            localStorage.setItem(imageURL,getBase64Image(ref.current));
        }catch{
            localStorage.clear();
        }
        
        setLoading(false);
    }
    
    return (<div className="image-holder relative">
        <img ref={ref} src={imgSrc} onLoad={e=>imageIsLoaded()} crossOrigin="anonymous" />
        {loading && <Spinner />}
    </div>)
};

export function getBase64Image(img) {
    // Create an empty canvas element
    var canvas = document.createElement("canvas");
    canvas.width = img.naturalWidth || img.width;
    canvas.height = img.naturalHeight || img.height;

    // Copy the image contents to the canvas
    var ctx = canvas.getContext("2d");
    ctx.drawImage(img, 0, 0);

    // Get the data-URL formatted image
    // Firefox supports PNG and JPEG. You could check img.src to
    // guess the original format, but be aware the using "image/jpg"
    // will re-encode the image.
    var dataURL = canvas.toDataURL("image/png");
   
    return dataURL //.replace(/^data:image\/(png|jpg);base64,/, "");
}

function PDFList(){
    
    const pdf = useSazaState(state=>state.acc?.data?.pdf);
    
    if(Array.isArray(pdf)){
        if(pdf.length === 0){
            return <></>
        }
        return (<div className="directory">
            <div className="directory-header" style={{fontSize:'1rem'}}>Report in PDF :</div>
            {pdf.map((pdfItem,index)=>(
                <div key={index} className="pdflink">
                    <a href={pdfItem.url} >View report</a>
                </div>
            ))}
        </div>)
    }

    return <></>

}



export default AccItems;