import { useEffect } from "react";

function Index(){
    useEffect(()=>{
        const time = setTimeout(()=>{
            window.location.reload();
        },3000);
        return ()=>clearTimeout(time);
    },[])

    return <div className=" full flex-center ">
        <div className="logo ">
            <div>
                <h1 className="loading">T I V A N</h1>
            </div>
        </div>
    </div>
}

export default Index;